<template>
    <div class="content-wrapper">
        <div class="content-header row mb-1">
            <breadcrumbs/>
        </div>
        <div class="content-body">
            <div class="row">
                <div class="col-12 col-sm-7 col-md-8">
                    <UsersList v-permission="'users.index'" :users="users"></UsersList>
                </div>
                <div class="col-12 col-sm-5 col-md-4">
                    <CreateForm v-permission="'users.create'" :roles="roles" @create="create"></CreateForm>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Axios from 'axios';
    import UsersList from './components/users_list';
    import CreateForm from './components/create_form';
    export default {
        name: "index",
        components: {
            CreateForm,
            UsersList
        },
        data: function() {
            return {
                users: [],
                roles: []
            }
        },
        async created() {

            // Получение пользователей.
            this.users = await Axios.get('users')
                .then(users => {return users.data});

            // Получение доступных ролей.
            this.roles = await Axios.get('users/roles')
                .then(roles => {return roles.data});

        },
        methods: {

            /**
             * Создание пользователя.
             * @param data
             * @returns {Promise<void>}
             */
            async create(data) {
                Axios.post('users', data)
                    .then(user => {
                        this.users.push(user);
                        this.$snotify.success(`Пользователь «${user.name}» успешно создан`);
                    });
            }
        }
    }
</script>

