<template>
    <Customizer :label="{ icon: 'ft-plus-square', bColor: 'bg-info' }" :open="open" @toggle="customizerToggle">
        <div class="customizer-content">
            <form v-on:submit.prevent="submit" class="position-relative">

                <!-- Загрузочный экран -->
                <loader :show="creation" :full="false"></loader>

                <!-- Input's для выбора изображений -->
                <input type="file" id="upload-image" class="upload-file-hidden" @change="uploadImage">
                <input type="file" id="upload-thumbnail-image" class="upload-file-hidden" @change="uploadThumbnailImage">

                <!-- URL сайта -->
                <div id="url">
                    <div>
                        <h5 class="text-uppercase">Добавление ссылки на сайт</h5>
                        <small>Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться.</small>
                    </div>
                    <fieldset class="form-group my-2">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span class="input-group-text"><i class="la la-file-code-o"></i></span>
                            </div>
                            <input type="text" class="form-control" placeholder="http://vsepuchkom.ru" v-model="site.url" required>
                        </div>
                        <fieldset class="form-group mt-2 mb-0 text-center">
                            <star-rating v-model="site.rating" :max-rating="10" :star-size="20" :inline="true" :show-rating="false" :increment="0.5"></star-rating>
                        </fieldset>
                    </fieldset>
                </div>

                <hr>

                <!-- Тип -->
                <div id="types">
                    <div>
                        <h5 class="text-uppercase">Выбор типа</h5>
                        <small>Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться.</small>
                    </div>
                    <fieldset class="form-group mt-1 mb-0">
                        <ul>
                            <li class="d-inline-block"
                                v-for="type in availableTypes"
                                v-bind:class="{ selected: type.id === site.type_id }"
                                @click="selectType(type)"
                            >{{ type.name }}</li>
                        </ul>
                    </fieldset>
                </div>

                <hr>

                <!-- Языки -->
                <div id="languages">
                    <div>
                        <h5 class="text-uppercase">Выбор языков</h5>
                        <small>Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться.</small>
                    </div>
                    <fieldset class="form-group mt-1 mb-0">
                        <ul>
                            <li class="d-inline-block"
                                v-for="language in availableLanguages"
                                v-bind:class="{ selected: site.languages.find(l => l === language) }"
                                @click="toggleLanguage(language)"
                            >{{ language.name }}</li>
                        </ul>
                    </fieldset>
                </div>

                <hr>

                <!-- Теги -->
                <div class="mt-2" id="tags">
                    <div class="mb-1">
                        <h5 class="text-uppercase">Добавление тегов</h5>
                        <small>Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться.</small>
                    </div>
                    <fieldset class="form-group" v-if="site.tags.length">
                        <ul>
                            <li class="d-inline-block selected font-small-1"
                                v-for="tag in site.tags"
                                @click="toggleTag(tag)"
                            >{{ tag.name }}</li>
                        </ul>
                    </fieldset>
                    <fieldset class="form-group mb-2">
                        <div class="creation-input-group input-group">
                            <input type="text" class="form-control" v-model="availableTagsInput">
                            <div class="input-group-append">
                                <button class="btn btn-info btn-sm" type="button" @click="createTag(availableTagsInput)">Создать</button>
                            </div>
                        </div>
                    </fieldset>
                    <fieldset class="form-group" v-if="availableTags.length">
                        <ul>
                            <li class="d-inline-block font-small-1"
                                v-for="availableTag in availableTags" :key="availableTag.id"
                                v-bind:class="{ selected: site.tags.find(t => t.id === availableTag.id)}"
                                @click="toggleTag(availableTag)"
                            >{{ availableTag.name }}</li>
                        </ul>
                    </fieldset>
                </div>

                <hr>

                <!-- Изображение -->
                <div id="images">
                    <div class="mb-1">
                        <h5 class="text-uppercase">Добавление изображений</h5>
                        <small>Давно выяснено, что при оценке дизайна и композиции читаемый текст мешает сосредоточиться.</small>
                    </div>
                    <ul v-if="site.images.length || site.thumbnail">
                        <li v-if="site.thumbnail" class="thumbnail">
                            <div class="name">{{ site.thumbnail.file.name }}</div>
                            <div class="other">{{ site.thumbnail.width }} x {{ site.thumbnail.height }} | {{ $formatSize(site.thumbnail.size) }}</div>

                            <div class="type">Обложка</div>
                            <div class="deletion-button" @click="deletionThumbnail"><i class="la la-close"></i></div>
                        </li>
                        <li v-if="site.images.length" v-for="image in site.images" class="image">
                            <div class="name">{{ image.file.name }}</div>
                            <div class="other">{{ image.width }} x {{ image.height }} | {{ $formatSize(image.size) }}</div>

                            <div class="type">Изображение</div>
                            <div class="deletion-button" @click="deletionImage(image)"><i class="la la-close"></i></div>
                        </li>
                    </ul>
                    <fieldset class="form-group mt-1 text-center">
                        <i class="thumbnail-creation-button la ft-plus-square font-large-1 cursor-pointer p-1" style="color:#a0a0a0;" onclick="document.getElementById('upload-thumbnail-image').click();"></i>
                        <i class="image-creation-button la ft-plus-square font-large-1 cursor-pointer p-1" style="color:#89d1ff;" onclick="document.getElementById('upload-image').click();"></i>
                    </fieldset>
                </div>

                <!-- Кнопки взаимодействия -->
                <div class="buttons mb-0">
                    <fieldset class="text-center">
                        <button class="btn btn-info mr-1" type="submit" style="width: 100%;">Создать</button>
                    </fieldset>
                </div>

            </form>
        </div>
    </Customizer>
</template>

<script>
    import Axios from 'axios';
    import StarRating from 'vue-star-rating'
    import Loader from '@/components/Loader';
    import Customizer from '@/components/Customizer';
    export default {
        name: "index",
        components: { Loader, Customizer, StarRating },
        props: {
            open: {
                type: Boolean,
                default: false
            }
        },
        data: function() {
            return {
                site: {
                    url: '',            // Адрес сайта (string)
                    rating: null,       // Рейтинг (float)
                    type_id: 1,         // Тип (int)
                    thumbnail: null,    // Миниатюра, обложка (object (with data) => file)
                    languages: [],      // Языки (arr[..id..])
                    images: [],         // Изображения (arr[file])
                    tags: [],           // Теги (arr[..id..])
                },

                // Хранение текста, введенного в input (тегов)
                availableTagsInput: '',

                // Временные теги хранятся тут.
                availableTags: [],

                // Все доступные языки.
                availableLanguages: [],

                // Все доступные типы.
                availableTypes: [],

                // Состояние загрузки сайта.
                creation: false

            }
        },
        methods: {

            /**
             * Формирование и отправка запроса на создание сайта.
             * @param event
             * */
            async submit(event) {
                try {

                    let data = new FormData();

                    // Обработка URL сайта.
                    if (!this.site.url.length) throw new Error('Не указан URL сайта');
                    else data.append('url', this.site.url);

                    // Обработка рейтинга.
                    if (!this.site.rating) throw new Error('Не указан рейтинг сайта');
                    else data.append('rating', this.site.rating);

                    // Обработка языков
                    if (!this.site.languages.length) throw new Error('Не выбраны языки сайта');
                    else {
                        let languages = await this.site.languages.map(s => {return s.id});
                        languages.forEach(language => {data.append('languages[]', language)});
                    }

                    // Обработка тегов.
                    if (!this.site.tags.length) throw new Error('Не указаны теги сайта');
                    else {
                        let tags = await this.site.tags.map(t => {return t.id});
                        tags.forEach(tag => {data.append('tags[]', tag)});
                    }

                    // Обработка изображений.
                    if (!this.site.images.length) throw new Error('Не выбраны изображения');
                    else {
                        let images = await this.site.images.map(i => {return i.file});
                        images.forEach(image => {data.append('images[]', image)});
                    }

                    // Обработка миниатюры (обложки)
                    if (!this.site.thumbnail) throw new Error('Не выбрана миниатюра сайта');
                    else data.append('thumbnail', this.site.thumbnail.file);

                    // Добавление типа.
                    data.append('type_id', this.site.type_id);


                    // Состояние загрузки.
                    this.creation = true;

                    // Передача запроса на сторону сервера.
                    await Axios.post('temp/sites', data, {
                        headers: {'Content-Type': 'multipart/form-data'}
                    }).then(site => {

                        // Отображение уведомления и закрытие кастомайзера.
                        this.$snotify.success(`${this.site.url} успешно добавлен`);
                        this.$emit('created', site);
                        setTimeout(() => {
                            this.customizerToggle();
                        }, 1000);

                    });

                } catch (e) {
                    if (e.name === 'Error') this.$snotify.error(e.message);
                } finally {
                    this.creation = false;
                }

            },

            /**
             * Выбор типа сайта.
             * @param type
             */
            selectType(type) { this.site.type_id = type.id },

            /**
             * Выбор языка.
             * @param language
             */
            toggleLanguage(language) {
                let index = this.site.languages.findIndex(l => l === language);
                if (index !== -1) this.site.languages.splice(index, 1);
                else this.site.languages.push(language);
            },

            /**
             * Присоединение / отсоединение тега.
             * @param tag
             * */
            toggleTag(tag) {
                let index = this.site.tags.findIndex(t => t === tag);
                if (index !== -1) this.site.tags.splice(index, 1);
                else this.site.tags.push(tag);
            },

            /**
             * Добавление нового тега.
             * Отправка запроса на сервер, получение тега - добавление к сайту.
             * @param name
             * */
            async createTag(name) {
                let tag = await Axios.post('temp/tags', { name }).then(tag => {return tag});
                this.site.tags.push(tag);
            },

            /**
             * Загрузка доступных тегов на освнове ввода.
             * */
            async loadAvailableTags(search) {
                if (!search.length) this.availableTags = [];
                else this.availableTags = await Axios.get(`temp/tags?search=${search}`).then(tags => {return tags.data});
            },

            /**
             * Загрузка изобажения
             * @param event
             * */
            async uploadImage(event) {

                // Получение изображения от события и проверка существования типа (image/*)
                // В данный сервис пользователь может загружать только изображения PNG, JPG, JPEG
                let image = event.target.files[0];
                if (!image) return;
                if(image.type.indexOf('image/') !== 0) throw new Error('Изображение не найдено или имеет не верный тип');

                // Получение данных изображения.
                image = await this.processFile(image).then(image => { return image; });
                this.site.images.push(image);
            },

            /**
             * Загрузка миниатюрного изобажения (обложки)
             * @param event
             * */
            async uploadThumbnailImage(event) {

                // Получение изображения от события и проверка существования типа (image/*)
                // В данный сервис пользователь может загружать только изображения PNG, JPG, JPEG
                let image = event.target.files[0];
                if (!image) return;
                if(image.type.indexOf('image/') !== 0) throw new Error('Изображение не найдено или имеет не верный тип');

                // Получение данных изображения.
                image = await this.processFile(image).then(image => { return image; });
                this.site.thumbnail = image;
            },

            /**
             * Удаление изображения
             * @param image
             * */
            deletionImage(image) {
                let index = this.site.images.findIndex(i => i.name === image.name);
                if (index !== -1) this.site.images.splice(index, 1);
                else throw new Error('Изображение не найдено');
            },

            /**
             * Удаление миниатюры (обложки)
             * */
            deletionThumbnail() {
                this.site.thumbnail = null;
            },

            /**
             * Обработка изобжения.
             * @param file
             * @returns {boolean}
             */
            async processFile(file) {
                return new Promise((resolve, reject) => {
                    try {

                        let image = {};
                        let reader = new FileReader();

                        reader.readAsDataURL(file);
                        reader.onload = e => {

                            let img = new Image();
                            img.src = e.target.result;
                            img.onload = () => {

                                image.file          = file;
                                image.width         = img.width;
                                image.height        = img.height;
                                image.size          = file.size;
                                image.url           = URL.createObjectURL(file);
                                image.extension     = file.name ? file.name.split('.').pop().toLowerCase() : null;

                                resolve(image)
                            };
                        };

                    } catch (e) {
                        reject(e);
                    }
                });
            },

            /**
             * Очистка всех данных компонента.
             * */
            reset() {
                let defaultData = this.$options.data.apply(this);
                defaultData.availableTypes = this.availableTypes;
                defaultData.availableLanguages = this.availableLanguages;
                Object.assign(this.$data, defaultData);
            },

            /**
             * Открытие / закрытие компонента.
             * */
            customizerToggle() {
                if (this.open) this.reset();
                this.$emit('toggle');
            }

        },

        /**
         * Получение необходимых данных
         * на этапе создания экземпляра компонента.
         * @returns {Promise<void>}
         */
        async created() {
            this.availableTypes = await Axios.get('temp/types').then(types => {return types.data});
            this.availableLanguages = await Axios.get('temp/languages').then(languages => {return languages.data});
        },

        watch: {

            /**
             * Прослушаивание изменения Input
             * для отображения доступных для выбора тегов.
             */
            availableTagsInput() {
                this.loadAvailableTags(this.availableTagsInput);
            }
        }

    }
</script>

<style lang="scss">
    .upload-file-hidden {
        position: absolute; top: 0;
        left: 0; display: none;
    }
    #types, #languages, #tags {
        ul {
            li {
                display: inline-block;
                padding: 5px 10px;
                background-color: #ececec;
                border-radius: 3px;
                font-size: 0.8rem;
                margin: 0 8px 6px 0;
                transition: 0.1s;
                cursor: pointer;

                &:hover {background-color: #d4d4d4;}
                &.selected {background-color: #eccb55; color: #fff;}
            }
        }
    }
    #images {
        ul {
            li {
                position: relative;
                padding: 4px 10px;
                border-radius: 3px;
                font-size: 0.9rem;
                margin-top: 8px;

                &.thumbnail {background-color: #a0a0a0;color: #ffffff;}
                &.image {background-color: #89d1ff;color: #ffffff;}
                &:first-child {margin-top: 0;}
                .type {
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding: 2px 6px;
                    font-size: 0.7rem;
                }
                .deletion-button {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    padding: 2px 6px;
                    font-size: 0.7rem;
                    cursor: pointer;
                }
            }
        }
        .image-creation-button {
            transition: .1s;
            &:hover {
                color: #4c5382;
            }
        }
        .thumbnail-creation-button {
            color: #a0a0a0;
            transition: .1s;
            &:hover {
                color: #a0a0a0;
            }
        }
    }
</style>