<template>
    <div class="temp-site-list-wrap">

        <div class="temp-site-empty-message my-3" v-if="!sites.length">
            <p class="text-uppercase text-center">Ожидающие обработки сайты не найдены</p>
        </div>
        <div class="temp-site-list row" v-if="sites.length">
            <div class="col-6 col-sm-4 col-md-3" v-for="site in sites" :key="site.id">
                <SiteMiniature :site="site" @delete="deletion"></SiteMiniature>
            </div>
        </div>
    </div>
</template>

<script>
    import Axios from 'axios';
    import SiteMiniature from '@/components/TempSiteMiniature';
    export default {
        name: "list",
        components: { SiteMiniature },
        methods: {
            deletion(site) {
                Axios.delete(`temp/sites/${site.id}`)
                    .then(() => {
                        let index = this.sites.findIndex(s => s.id === site.id);
                        this.sites.splice(index, 1);
                        this.$snotify.success('Сайт успешно удален');
                    })
            }
        },
        props: {
            sites: Array
        }
    }
</script>

