<template>
    <div class="services-images-wrap">

        <!-- Uploading image to preview loader -->
        <loader :show="uploadingImage" :full="false"></loader>

        <div class="services-images" v-if="!uploadingImage">
            <div class="row">

                <!-- Form request -->
                <form v-on:submit.prevent="submit" class="d-block">

                    <!-- Input file hidden -->
                    <input type="file" id="file" @change="upload">
                    <div class="col-12" v-if="!uploadedImage">
                        <div class="form-group">
                            <a href="javascript:void(0);" class="btn btn-float btn-float-lg btn-pink" onclick="document.getElementById('file').click();">
                                <i class="la la-cloud-upload"></i>
                                <span>Выбрать</span>
                            </a>
                        </div>
                    </div>

                    <!-- Image preview -->
                    <div class="col-12 image mt-2" v-if="uploadedImage">
                        <img :src="originalImage.url" />
                    </div>

                    <!-- Option fields -->
                    <Customizer title="Настройки экспорта" v-if="uploadedImage" :open="customizerShow" @toggle="customizerShow = !customizerShow">
                        <customizer-before :originalImage="originalImage" :options="options" @submit="submit" v-if="!compressedImage" @reset="reset"></customizer-before>
                        <customizer-after :originalImage="originalImage" :responseImage="responseImage" v-if="compressedImage" @download="download" @cancel="cancel"></customizer-after>
                        <loader :show="sendRequest" :full="false"></loader>
                    </Customizer>
                </form>

            </div>
        </div>

    </div>
</template>

<script>
    import Axios from 'axios';
    import Loader from '@/components/Loader';
    import Customizer from '@/components/Customizer';
    import CustomizerAfter from './customizer.after';
    import CustomizerBefore from './customizer.before';
    export default {
        name: "services.images",
        components: {
            Loader,
            Customizer,
            CustomizerAfter,
            CustomizerBefore
        },
        data: function() {
            return {

                // Оригинальное изображение,
                // загружаемое пользователем в сервис.
                originalImage: {},

                // Изображение, полученное от
                // сервера после преобразования.
                responseImage: {},

                // Опции преобразования.
                options: {
                    width:      null,
                    height:     null,
                    quality:    100,
                    extension:  'jpg'
                },

                // Состояние загрузки изображения в сервис клиентом.
                uploadingImage: false,
                uploadedImage:  false,

                // Состояние отправки изображения на сервер для преобразования.
                sendRequest:    false,
                compressedImage:false,
                customizerShow: false
            }
        },
        methods: {


            /**
             * Загрузка изображения пользователем
             * Обработка изображения и внедрение данных в состояние.
             * */
            async upload(event) {
                try {

                    // Установка состояния загрузки изображения клиентом (для отображения loading)
                    this.uploadingImage = true;

                    // Получение изображения от события и проверка существования типа (image/*)
                    // В данный сервис пользователь может загружать только изображения PNG, JPG, JPEG
                    let image = event.target.files[0];
                    if(!image || image.type.indexOf('image/') !== 0) throw new Error('Изображение не найдено или имеет не верный тип');

                    // Получение данных изображения.
                    this.originalImage = await this.processFile(image).then(image => { return image; });

                } catch (e) {

                    this.$snotify.error(e.message);
                    return false;

                } finally {

                    setTimeout(() => {
                        // Завершение состояния загрузки изображения клиентов.
                        this.uploadingImage = false;
                        this.uploadedImage  = true;
                    }, 3000);

                }
            },

            /**
             * Отравка формы на сервер
             * для изменения изображения
             * */
            submit(event) {

                try {

                    let formData = new FormData();
                    formData.append('file', this.originalImage.file);
                    formData.append('quality', this.options.quality);

                    if (this.options.width) formData.append('width', this.options.width);
                    if (this.options.height) formData.append('height', this.options.height);
                    if (this.options.extension) formData.append('extension', this.options.extension);

                    // Установка состояния отправки запроса на сервер.
                    this.sendRequest = true;

                    // Отправка запроса.
                    Axios.post('services/image/compression', formData, {
                        responseType: 'blob',
                        headers: {'Content-Type': 'multipart/form-data'}
                    }).then(async image => {

                        // Сохранение изображения в состоянии.
                        this.responseImage = await this.processFile(image).then(image => {return image});
                        this.compressedImage = true;

                    }).finally(() => {

                        // Отключение состояния загрузки данных от сервера.
                        setTimeout(() => { this.sendRequest = false }, 2000);

                    });

                } catch (e) {
                    this.$snotify.error(e.message);
                    return false;
                }

            },

            /**
             * Обработка изобжения.
             * @param file
             * @returns {boolean}
             */
            async processFile(file) {
                return new Promise((resolve, reject) => {
                    try {

                        let image = {};
                        let reader = new FileReader();

                        reader.readAsDataURL(file);
                        reader.onload = e => {

                            let img = new Image();
                            img.src = e.target.result;
                            img.onload = () => {

                                image.file          = file;
                                image.width         = img.width;
                                image.height        = img.height;
                                image.size          = file.size;
                                image.url           = URL.createObjectURL(file);
                                image.extension     = file.name ? file.name.split('.').pop().toLowerCase() : null;

                                resolve(image)
                            };
                        };

                    } catch (e) {
                        reject(e);
                    }
                });
            },

            /**
             * Скачивание изображения
             * Создание <a> ссылки на скачивание, помещение ее на экран,
             * Клик через 33 мсек.
             * */
            download() {

                // Создание элемента ссылки.
                let link = document.createElement("a");
                link.href = (window.webkitURL || window.URL).createObjectURL(this.responseImage.file);

                // Генерирование имени файла и расширения.
                let name = this.originalImage.file.name.substring(0, this.originalImage.file.name.lastIndexOf('.'));
                let extension = this.options.extension ? this.options.extension : 'jpg';
                link.download = `${name}_thumbnail.${extension}`;

                // Добавление ссылки на страницу.
                document.body.appendChild(link);

                // Клик и удаление ссылки.
                setTimeout(() => {
                    link.click();
                    document.body.removeChild(link);
                }, 33);

            },

            /**
             * Отмена customizer.after (отображения окна отчета и возможности скачивания)
             * Устанавливая compressedImage в false, убираем состояние загруженного файла от сервера
             * чем вызываем customizer.after отключиться
             */
            cancel() {

                // Устанавливая compressedImage в false, убираем состояние загруженного файла от сервера
                // чем вызываем customizer.after отключиться
                this.compressedImage = false;

            },

            /**
             * Сброс компонента, всех его установленных значений.
             */
            reset() {
                this.originalImage      = {};
                this.responseImage      = {};
                this.options = {
                    width:      null,
                    height:     null,
                    quality:    100,
                    extension:  'jpg'
                };
                this.uploadingImage     = false;
                this.uploadedImage      = false;
                this.sendRequest        = false;
                this.compressedImage    = false;
            }

        }
    }
</script>

<style lang="scss">
    .services-images-wrap {
        .services-images {
            form {
                width: 100%;

                input[type=file] {
                    position: absolute;
                    top: 0;
                    left: 0;
                    opacity: 0;
                    z-index: -1;
                }

                .image {
                    img {width: 100%;}
                }
            }
        }
    }
</style>