<template>
    <div class="page" id="sites">

        <!-- Загрузочный экран -->
        <Loader :show="!loaded" :full="false"></Loader>

        <!-- Отображение списка сайтов -->
        <List :sites="sites"></List>

        <!-- Customizer для добавления сайтов -->
        <SiteCreation :open="siteCreationShow" @toggle="siteCreationShow = !siteCreationShow"></SiteCreation>
    </div>
</template>

<script>
    import Axios from 'axios';
    import List from './components/list';
    import Loader from '@/components/Loader';
    import SiteCreation from '@/customizers/site.creation';
    export default {
        name: "index",
        components: { Loader, SiteCreation, List },
        data: function() {
            return {
                sites: [],
                types: [],
                siteCreationShow: false,
                loaded: false
            }
        },
        async created() {

            // Получение доступных типов.
            this.types = await Axios.get('main/types').then(types => {return types.data});
            // Получение сайтов.
            this.sites = await Axios.get('main/sites').then(sites => {return sites.data});
            // Отключение загрузочного экрана.
            this.loaded = true;
        }
    }
</script>

