<template>
    <div class="page" id="sites-show">

        <!-- Загрузочный экран -->
        <loader :show="!loaded" :full="false"></loader>

        <!-- Информация о сайте -->
        <div class="site-info row" v-if="site">
            <div class="col-12 col-md-7 mb-2">
                <div class="preview" >
                    <img class="thumbnail" :src="site.thumbnail"/>
                </div>
            </div>
            <div class="col-12 col-md-5">
                <fieldset class="form-group" id="url">
                    <h5>URL</h5>
                    <p class="value mb-0"><a :href="site.url" target="_blank">{{ site.url }}</a></p>
                </fieldset>
                <fieldset class="form-group" id="languages">
                    <h5>Языки</h5>
                    <ul>
                        <li v-for="language in site.languages.data">{{ language.name }}</li>
                    </ul>
                </fieldset>
                <fieldset class="form-group" id="tags">
                    <h5>Теги</h5>
                    <ul>
                        <li v-for="tag in site.tags.data">{{ tag.name }}</li>
                    </ul>
                </fieldset>
            </div>
        </div>
    </div>
</template>

<script>
    import Axios from 'axios';
    import Loader from '@/components/Loader';
    export default {
        name: "show",
        components: { Loader },
        props: {
            id: {required: true}
        },
        data() {
            return {
                site: null,
                loaded: false
            }
        },
        async created() {

            // Получение сайта от сервера.
            this.site = await Axios.get(`main/sites/${this.id}`).then(site => {return site});
            this.loaded = true;
        }
    }
</script>

<style lang="scss">
    #sites-show {
        .site-info {
            .preview {
                height: 75vh;
                overflow: scroll;
                img {width: 100%;}
            }
        }
        fieldset {
            ul {
                li {
                    display: inline-block;
                }
            }
        }
    }
</style>