<template>
    <div class="page" id="profile">

        <!-- Показатели -->
        <div class="row">
            <div class="col-xl-3 col-lg-6 col-12">
                <div class="card pull-up">
                    <div class="card-content">
                        <div class="card-body pb-1">
                            <div class="media d-flex">
                                <div class="media-body text-left">
                                    <h3 class="info">{{ profile.published_sites.length }}</h3>
                                    <h6 class="text-uppercase">Добавленные</h6>
                                </div>
                                <div>
                                    <i class="icon-basket-loaded info font-large-2 float-right"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-xl-3 col-lg-6 col-12">
                <div class="card pull-up">
                    <div class="card-content">
                        <div class="card-body pb-1">
                            <div class="media d-flex">
                                <div class="media-body text-left">
                                    <h3 class="warning">{{ profile.sites.length }}</h3>
                                    <h6 class="text-uppercase">В обработке</h6>
                                </div>
                                <div>
                                    <i class="icon-clock warning font-large-2 float-right"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-xl-3 col-lg-6 col-12">
                <div class="card pull-up">
                    <div class="card-content">
                        <div class="card-body pb-1">
                            <div class="media d-flex">
                                <div class="media-body text-left">
                                    <h3 class="success">{{ profile.paid_out }}₽</h3>
                                    <h6 class="text-uppercase">Выплачено</h6>
                                </div>
                                <div>
                                    <i class="icon-wallet success font-large-2 float-right"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-xl-3 col-lg-6 col-12">
                <div class="card pull-up">
                    <div class="card-content">
                        <div class="card-body pb-1">
                            <div class="media d-flex">
                                <div class="media-body text-left">
                                    <h3 class="danger">{{ profile.balance }}₽</h3>
                                    <h6 class="text-uppercase">Баланс</h6>
                                </div>
                                <div>
                                    <i class="icon-present danger font-large-2 float-right"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Отображение списка сайтов -->
        <div class="row">
            <div class="col-12">
                <list :sites="sites"></list>
            </div>
        </div>

        <!-- Customizer для добавления сайтов -->
        <SiteCreation :open="siteCreationShow" @toggle="siteCreationShow = !siteCreationShow" @created="addSite"></SiteCreation>

    </div>
</template>

<script>
    import Axios from 'axios';
    import List from './components/list';
    import SiteCreation from '@/customizers/site.creation';
    export default {
        name: "index",
        components: { SiteCreation, List },
        data() {
            return {
                sites: [],
                siteCreationShow: false
            }
        },
        computed: {
            profile() {
                return this.$store.state.user.profile;
            }
        },
        methods: {
            addSite(site) {
                this.sites.unshift(site);
                this.$store.dispatch('ADD_TEMP_SITE', site);
            }
        },
        async created() {
            this.sites = await Axios.get('temp/sites')
                .then(sites => {return sites.data.reverse()})
        }
    }
</script>

