<template>
    <div class="card">
        <div class="card-header">
            <h4 class="card-title">Создание</h4>
        </div>
        <div class="card-body">
            <form @submit.prevent="submit">
                <div class="row">
                    <div class="col-12">
                        <fieldset class="form-group">
                            <small>ИМЯ ПОЛЬЗОВАТЕЛЯ</small>
                            <input type="text" v-model="name" class="form-control" placeholder="Иван" required>
                        </fieldset>
                    </div>
                    <div class="col-12">
                        <fieldset class="form-group">
                            <small>РОЛЬ</small>
                            <v-select label="display_name" v-model="role_id" :reduce="role => role.id" :options="roles" :clearable="false" req></v-select>
                        </fieldset>
                    </div>
                </div>
                <div class="row">
                    <div class="col-12">
                        <fieldset class="form-group">
                            <small>EMAIL</small>
                            <div class="input-group">
                                <div class="input-group-prepend"><span class="input-group-text"><i class="la la-envelope"></i></span></div>
                                <input type="email" v-model="email" class="form-control" placeholder="ivan@mail.ru" required>
                            </div>
                        </fieldset>
                    </div>
                    <div class="col-12">
                        <fieldset class="form-group">
                            <small>ПАРОЛЬ</small>
                            <div class="input-group">
                                <div class="input-group-prepend"><span class="input-group-text"><i class="la la-key"></i></span></div>
                                <input type="text" v-model="password" class="form-control" required>
                            </div>
                        </fieldset>
                    </div>
                </div>
                <div class="row">
                    <div class="col-12">
                        <button type="submit" class="btn btn-info btn-min-width">Создать</button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "create_form",
        props: {
            roles: Array
        },
        data: function() {
            return  {
                name: '',
                email: '',
                password: '',
                role_id: undefined
            }
        },
        methods: {
            submit: function(event) {
                let data = {name: this.name, email: this.email, password: this.password, role_id: this.role_id};
                this.$emit('create', data);
            }
        }
    }
</script>

